<template>
  <div id="main">
    <ul id="list">
      <li>
        <span>序列</span><span>歌曲</span><span>歌手</span><span>专辑</span>
      </li>
      <li @click="player(song.id)" v-for="(song, index) in result" :key="index">
        <span>{{ index + 1 }}</span
        ><span>{{ song.name }}</span
        ><span>{{ song.artists[0].name }}</span
        ><span>{{ song.album.name }}</span>
      </li>
    </ul>
    <div id="img">
      <img src="@/assets/background/img-insert/默认歌单.png" alt="" />
    </div>
  </div>
</template>

<script>
import axios from "axios";
export default {
  data() {
    return {
      id: 0,
      // data: {},
      url: "",
    };
  },
  props: ["result"],
  methods: {
    player(data) {
      this.id = data;
      axios
        .get(`https://music.cyrilstudio.top/song/url?id=${this.id}`)
        .then((response) => {
          // console.log(response.data);
          // this.data = response.data;
          this.url = response.data.data[0].url;
          this.$bus.$emit("receiveUrl", this.url);
        });
    },
  },
};
</script>

<style scoped>
#img {
  flex: 2;
  margin-left: 5%;
}
#img img {
  width: 50%;
  max-height: 100%;
}

#main {
  display: flex;
  justify-content: flex-start;
  height: 500px;
}
#list {
  height: 100%;
  margin-left: 15%;
  flex: 5;
  overflow: scroll;
}
#list li {
  display: flex;
  justify-content: flex-start;
  color: rgba(225, 225, 225, 0.8);
  font-weight: 200;
  font-size: 18px;
  /* width: 60%; */
  margin: 10px;
  height: 40px;
  border-bottom: 2px solid rgba(255, 255, 255, 0.4);
}
#list li:hover {
  cursor: pointer;
  background-color: rgba(128, 128, 128, 0.6) !important;
}
#list span:nth-child(1) {
  flex: 0.5;
}
#list span:nth-child(2) {
  flex: 1;
}
#list span:nth-child(3) {
  flex: 1;
}
#list span:nth-child(4) {
  flex: 1.5;
}
</style>